$colorLightBlue: #79c6d0;
$colorBlue: #3495ca;
$colorLightGray: #eaeef1;

@mixin for-sm-screen(){
  @media (min-width: 768px){
    &{
      @content;
    }
  }
}
@mixin for-md-screen(){
  @media (min-width: 992px){
    &{
      @content;
    }
  }
}
@mixin for-xl-screen(){
  @media (min-width: 1200px){
    &{
      @content;
    }
  }
}

