html {
  height: 100%;
}

body {
  background: url('../img/bg.png');
  background-color: $colorLightGray;
  background-size: 1980px auto;
  background-position: center -50px;
  background-repeat: no-repeat;
  min-height: 100%;
}

.header {
  overflow: hidden;
  height: 111px;
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.2);
  background: $colorLightBlue;
  margin-bottom: 15px;

}

.wrapper {
  background: rgba(255, 255, 255, 0.9);
  min-height: 400px;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.2);
}

.main-header {
  height: 145px;
  padding-top: 35px;
  &-logo__img {
    margin-left: 30px;
    display: block;
    float: left;
  }
  &-logo__title {
    display: block;
    text-align: left;
    padding-top: 20px;
    margin-left: 10px;
    float: left;
  }
  &__phone {
    display: block;
    height: 100%;
    padding-top: 52px;
    font-size: 20px;
    text-align: left;
  }
  &__info {
    display: block;
    height: 100%;
    padding-top: 0px;
    text-align: left;
    float: left;
  }
  &__link--exit {
    display: block;
    background: $colorLightBlue;
    color: #ffffff;
    text-align: center;
  }

}
