.btn {
  padding: 8px 20px;
}

.btn-default {
  color: #ffffff;
  background-color: $colorBlue;
  border-color: $colorBlue;
  &:hover {
    color: #ffffff;
    background-color: $colorLightBlue;
    border-color: $colorLightBlue;
  }
}

.navbar-default.navbar {
  //height: 48px;
  .navbar-collapse {
    padding: 0;
    background-color: $colorBlue;
    //height: 48px !important;
  }
  .navbar-header {
    background-color: $colorBlue;
    .navbar-brand {
      color: #ffffff;
    }
    .navbar-toggle {
      border-color: #ffffff;
      .icon-bar {
        background: #ffffff;
      }
      &:hover, &:focus {
        background-color: $colorLightBlue;
      }
    }
  }
  .navbar-nav {
    width: 100%;
    margin: 0;
    @include for-sm-screen() {
      height: 50px;
    }
    > li {
      color: #ffffff;
      text-align: center;
      position: static;
      a {
        color: #ffffff;
      }
      @include for-sm-screen() {
        width: #{100%/7};
        height: 50px;
        display: table;
        vertical-align: bottom;
        > a{
          padding: 0;
          height: 100%;
          display: table-cell;
          vertical-align: middle;
        }
      }
    }
    > li.active > a, > li.open > a, > li:hover > a, > li:focus > a {
      background-color: $colorLightBlue;
      color: #ffffff;
    }
  }
  .dropdown-menu {
    width: 100%;
    border: none;
    box-shadow: none;
    padding: 0;
    //height: 48px;
    background-color: $colorLightBlue;
    border-radius: 0;
    > li, > li:hover, > li.active {
      text-align: center;
      @include for-sm-screen() {
        float: left;
        width: #{100%/5};
      }
      @include for-md-screen() {
        width: #{100%/6};
      }
      @include for-xl-screen() {
        width: #{100%/7};
      }
      > a {
        color:#ffffff !important;
        padding: 3px 0px;
        background-color: $colorLightBlue;
        height: 48px;
        line-height: 48px;
        &:hover {
          font-weight: bold;
        }
      }
    }
  }
}